.dbo__gallerypage .dbo__navbar {
    background-color: var(--color-dominant);
}




.dbo__gallerypage-container_gallery {
    -webkit-column-count: 3;
    -moz-column-count: 3; 
    column-count: 3;
    
    -webkit-column-width: 33%;
    -moz-column-width: 33%;
    column-width: 33%;
    margin: 1.5rem;
    padding: 0 1rem;
}

.dbo__gallerypage-container_gallery-image {
    -webkit-transition: all 350ms ease;
    transition: all 350ms ease;
    cursor: pointer;
    margin-bottom: 12px;
}

.dbo__gallerypage-container_gallery-image:hover {
    filter: opacity(.8);
}

@media screen and (max-width: 991px) {
    .dbo__gallerypage-container_gallery {
        -webkit-column-count: 2;
    -moz-column-count: 2; 
    column-count: 2;
    }
}

@media screen and (max-width: 480px) {
    .dbo__gallerypage-container_gallery {
        -webkit-column-count: 1;
        -moz-column-count: 1; 
        column-count: 1;
        
        -webkit-column-width: 100%;
        -moz-column-width: 100%;
        column-width: 100%;
    }
    
}

/* Pop-Out Photo */

.model {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, .8);
    transition: opacity .4s ease, visibility .4s ease, transform .5s ease-in-out;
    visibility: hidden;
    opacity: 0;
    transform: scale(0);
    overflow: hidden;
    z-index: 999;
}

.model.open {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
}

.model.open svg {
    position: fixed;
    top: 10px;
    right: 10px;
    width: 2rem;
    height: 2rem;
    padding: 5px;
    
    color: var(--color-light_text);
    cursor:pointer;

}

.model img {
    width: auto;
    max-width: 100%;
    height: auto;
    max-height: 100%;
    display: block;
    line-height: 0;
    box-sizing: border-box;
    padding: 20px 0 20px;
    margin: 0 auto;
}
